html {
  font-size: 14px;
}
body {
  font-family: "Roboto", "Arial", sans-serif;
  color: #333333;
  font-size: 1rem;
  line-height: 1.4;
  -webkit-font-smoothing: antialiased;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.06);
}
@font-face {
  font-family: icon-font;
  src: url(../fonts/icon-font/fontello.eot);
  /* IE9 Compat Modes */
  src: url(../fonts/icon-font/fontello.eot?#iefix) format('embedded-opentype'), /* IE6-IE8 */ url(../fonts/icon-font/fontello.woff) format('woff'), /* Pretty Modern Browsers */ url(../fonts/icon-font/fontello.ttf) format('truetype'), /* Safari, Android, iOS */ url(../fonts/icon-font/fontello.svg#svgFontName) format('svg');
  /* Legacy iOS */
}
button {
  background-color: transparent;
}
.relative {
  position: relative;
}
.text-right {
  text-align: right;
}
.text-left {
  text-align: left;
}
.text-center {
  text-align: center;
}
.m0a {
  margin: 0 auto;
}
.white {
  color: #fff;
}
.container-fluid {
  min-width: 320px;
  max-width: 1170px;
}
.container-fluid.container-full {
  margin-right: auto;
  margin-left: auto;
  padding-left: 20px;
  padding-right: 20px;
  max-width: 1500px;
}
.row--m-0 {
  margin: 0;
}
.link {
  text-decoration: none;
  color: #a96566;
  border-bottom: 1px solid rgba(169, 101, 102, 0.4);
  transition: all .4s;
}
.link:hover {
  color: #3096bc;
  border-bottom: 1px solid rgba(48, 150, 188, 0.4);
  transition: all .1s;
  cursor: pointer;
  text-decoration: none;
}
.link--white {
  color: #fff;
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
}
.link--white:hover {
  color: #62b7d7;
  border-bottom: 1px solid rgba(77, 174, 210, 0.4);
}
.link--pseudo {
  border-bottom: 1px dashed rgba(169, 101, 102, 0.4);
}
.link--pseudo.link--white:hover {
  border-bottom: 1px dashed rgba(187, 134, 135, 0.4);
}
.h2 {
  font-weight: 100;
  font-size: 2.5rem;
  margin: .67em 0 .5em;
}
.header {
  padding: 14px;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.05);
  position: relative;
  z-index: 6;
}
.header__btn {
  color: rgba(169, 101, 102, 0.8);
  display: inline-block;
  text-decoration: none;
  border: 2px solid rgba(169, 101, 102, 0.5);
  border-radius: 22px;
  padding: 9px 30px;
  position: relative;
  transition: all .4s;
}
.header__btn:before {
  content: '';
  position: absolute;
  display: block;
  width: 12px;
  height: 12px;
  border-top: 2px;
  border-right: 2px;
  border-top-color: rgba(169, 101, 102, 0.5);
  border-right-color: rgba(169, 101, 102, 0.5);
  border-top-style: solid;
  border-right-style: solid;
  left: 22px;
  top: 13px;
  transition: all .4s;
}
.header__btn--prev {
  padding-left: 40px;
}
.header__btn--prev:before {
  transform: rotate(-135deg);
}
.header__btn--next {
  padding-right: 40px;
}
.header__btn--next:before {
  left: auto;
  right: 22px;
  transform: rotate(45deg);
}
.header__btn:hover {
  text-decoration: none;
  border: 2px solid #a96566;
  color: #a96566;
  transition: all .2s;
}
.header__btn:hover:before {
  border-top-color: #a96566;
  border-right-color: #a96566;
  transition: all .2s;
}
.footer {
  padding: 20px 0 30px;
}
.project__subtitle {
  font-size: 21px;
  font-weight: 300;
  margin: .67em 0 .5em;
}
.project__flow {
  font-size: 16px;
  margin: 18px 0 30px;
}
.project__madein {
  font-size: 12px;
  line-height: 1.5;
}
.project__madein--logomotiv {
  position: relative;
}
.project__madein--logomotiv:before {
  content: '';
  position: absolute;
  top: -20px;
  left: 0;
  width: 30px;
  height: 15px;
  background-image: url(../img/logomotiv__icon.png);
  background-size: cover;
  background-repeat: no-repeat;
}
@keyframes supheaderAnimation {
  from {
    background-position-y: 0%;
  }
  50% {
    background-position-y: 100%;
  }
  to {
    background-position-y: 0;
  }
}
.supheader {
  line-height: 0;
  height: 20px;
  background-image: url(../img/color-image.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  animation-duration: 20s;
  animation-fill-mode: both;
  animation-iteration-count: infinite;
  animation-name: supheaderAnimation;
}
.lang {
  opacity: .8;
  display: inline-block;
  vertical-align: middle;
  margin-left: 1em;
  font-size: 12px;
  line-height: 1.2em;
}
.lang:hover {
  opacity: 1;
}
.lang.active {
  opacity: 1;
  border-color: transparent;
}
.lang.active:hover {
  cursor: default;
  color: #fff;
}
